.Details {
  padding-top: 4rem;
  padding-bottom: 5rem;
}

.yotubeVideo {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 2rem;
  padding: 0rem 8rem;
}

.Youtube {
  margin: auto;
  padding: 1.8rem;
  width: 100%;
  border: 1px solid #232354;
  border-radius: .5rem;
}

.Youtube:hover {
  box-shadow: 0px 5px 20px -10px #232354;
}

.Youtube iframe {
  height: 10rem;
  border-radius: .5rem;
}

.Youtube div a {
  color: #232354;
  text-decoration: none;
}

.yotubeVideo .Youtube .Button {
  padding-top: 1rem;
}

.yotubeVideo .Youtube .Button a {
  border: 1px solid #232354;
  border-radius: .2rem;
}

.yotubeVideo .Youtube .Button a:hover {
  background-color: #232354;
  color: white;
}

@media screen and (max-width:1300px) {
  .yotubeVideo {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
  }

  .Youtube {
    width: 70%;
  }
}

@media screen and (max-width:1200px) {
  .Youtube {
    width: 85%;
  }
}

